"use client";

import { useState, useEffect } from "react";
import Link from "next/link";
import Image from "next/image";
import { Search, Bell, User } from "lucide-react";
import { UserButton, SignedIn, SignedOut, SignInButton } from "@clerk/nextjs";
import clsx from "clsx";
import { usePathname } from "next/navigation";

const NAV_LINKS = [
    { name: "Movies", href: "/movies" },
    { name: "TV Shows", href: "/tv" },
    { name: "Discover", href: "/discover" },
    { name: "Calendar", href: "/calendar" },
    { name: "Lists", href: "/lists" },
];

export function Navbar() {
    const [isScrolled, setIsScrolled] = useState(false);
    const pathname = usePathname();

    useEffect(() => {
        const handleScroll = () => {
            setIsScrolled(window.scrollY > 0);
        };
        window.addEventListener("scroll", handleScroll);
        return () => window.removeEventListener("scroll", handleScroll);
    }, []);

    return (
        <nav
            className={clsx(
                "fixed top-0 left-0 right-0 z-50 transition-all duration-300 border-b",
                isScrolled
                    ? "bg-black/70 backdrop-blur-xl border-white/5 py-4"
                    : "bg-transparent border-transparent py-6"
            )}
        >
            <div className="container mx-auto px-6 lg:px-20 flex items-center justify-between">
                {/* Logo */}
                <Link href="/" className="flex items-center gap-2 relative z-50">
                    {/* Reusing existing text logo for now if image not available, or assume standard typography */}
                    <span className="font-display font-bold text-2xl tracking-tight text-white">
                        The<span className="text-accent-primary">Movie</span>
                    </span>
                </Link>

                {/* Desktop Navigation */}
                <div className="hidden md:flex items-center gap-8 absolute left-1/2 transform -translate-x-1/2">
                    {NAV_LINKS.map((link) => {
                        const isActive = pathname === link.href;
                        return (
                            <Link
                                key={link.name}
                                href={link.href}
                                className={clsx(
                                    "text-sm font-medium transition-all relative group",
                                    isActive ? "text-white" : "text-text-secondary hover:text-white"
                                )}
                            >
                                {link.name}
                                {/* Active Indicator / Hover Glow */}
                                <span className={clsx(
                                    "absolute -bottom-1 left-0 w-full h-[2px] bg-accent-primary rounded-full transition-all duration-300",
                                    isActive ? "opacity-100 scale-x-100" : "opacity-0 scale-x-0 group-hover:opacity-50 group-hover:scale-x-50"
                                )} />
                                {isActive && (
                                    <span className="absolute inset-0 bg-accent-primary/20 blur-lg -z-10 rounded-full" />
                                )}
                            </Link>
                        );
                    })}
                </div>

                {/* Actions */}
                <div className="flex items-center gap-6">
                    <button className="text-text-secondary hover:text-white transition-colors">
                        <Search size={20} />
                    </button>
                    <button className="text-text-secondary hover:text-white transition-colors relative">
                        <Bell size={20} />
                        <span className="absolute -top-1 -right-1 w-2 h-2 bg-accent-primary rounded-full" />
                    </button>

                    <SignedIn>
                        <UserButton
                            appearance={{
                                elements: {
                                    avatarBox: "w-9 h-9 border-2 border-white/10 hover:border-text-primary transition-colors"
                                }
                            }}
                        />
                    </SignedIn>
                    <SignedOut>
                        <SignInButton mode="modal">
                            <button className="px-4 py-2 bg-white/10 hover:bg-white text-white hover:text-black rounded-lg text-sm font-bold transition-all backdrop-blur-md border border-white/10">
                                Sign In
                            </button>
                        </SignInButton>
                    </SignedOut>
                </div>
            </div>
        </nav>
    );
}
